        <div class="school">
            <p>
                <span v-html="sData.preTestSubject?sData.preTestSubject:''"></span>
            </p>
        </div>
        <h3 :style="{background: $store.state.themeColor,color:'#fff'}"></h3>
        <div class="school">
            <p>
                <span v-html="sData.reTestSubject?sData.reTestSubject:''"></span>
            </p>
        </div>
        <h3 :style="{background: $store.state.themeColor,color:'#fff'}"></h3>
        <div class="school">
            <p>
                <span v-html="sData.addTestSubject?sData.addTestSubject:''"></span>
            </p>
        </div>
        <h3 :style="{background: $store.state.themeColor,color:'#fff'}"></h3>
        <div class="school">
            <p>
                <span v-html="sData.preTestPlan?sData.preTestPlan:''"></span>
            </p>
        </div>
        <h3 :style="{background: $store.state.themeColor,color:'#fff'}"></h3>
        <div class="school">
            <p>
