            </p>
        </div>
    </div>
</template>
<script type="text/javascript">
    export default {
        data() {
            return {
                sData: null,
            }
        },
        created() {
            var state = this.$store.state
            var url = state.host + state.baseUrl + '/getMajorResearchDetail?id=' + this.id
            this.$http.get(url)
                .then(res => {
                    console.log(res.data)
                    this.secondCatName = '>>' + res.data.name;
                    this.sData = res.data
                })
        },
        props: ['id']
    }
</script>
<style lang="scss" scoped>
    @import "../css/common.scss";

    .info {
        h3,
        p {
            padding: pxToRem(10) pxToRem(40);
            font-size: pxToRem(32);
            border-bottom: pxToRem(1) solid #ddd;
            overflow: hidden;
            &:nth-of-type(2n) {
                background: #f1f1f1;
            }
            span {
                float: left;
                display: block;
            }
        }
        .school {
            p {
                span:first-child {
                    width: 90%;
                    // margin-right: pxToRem(20);
                }
                // span:last-child {
                //     width: 60%;
                // }
            }
        }
        .tData {
            p {
                span:first-child {
