<template>
    <h1>quickAsk</h1>
</template>
<script type="text/javascript">
    import {mapActions} from "vuex"
    import count from '../utils/count.js'
    import urlAddToken from '../utils/urlAddToken.js'

    export default {
        data() {
            return {}
        },
        methods: {
            ...mapActions(['CHANGE_FOOTERTAB'])
        },
        mounted: function () {
            this.CHANGE_FOOTERTAB(1)
            var _this = this;
            setTimeout(() => {
                count(remote_ip_info.province, remote_ip_info.city, localStorage.scode, location.href, null, null, _this.$store.state.viewSourceUrl).then(res => {
                });
            }, 1000)


            var state = this.$store.state;

            var url = state.host + state.baseUrl + '/getJsSign'
            var data = {
                url: location.href.indexOf('from=')>-1?location.href.split('&from=')[0]:location.href.split('#')[0]
            }
            this.$http.post(url, data, {
                emulateJSON: true
            })
                .then(res => {
                    // console.log(res.data)
                    var data = res.data
                    wx.config({
                        debug: false, // ,apialertpclogpc
                        appId: data.appId, // 
                        timestamp: data.timestamp, // 
                        nonceStr: data.nonce, // 
                        signature: data.signature, // 1
                        jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'startRecord', 'stopRecord', 'onVoiceRecordEnd', 'translateVoice'] // JSJS2
                    });

                    wx.ready(function () {
                        wx.onMenuShareTimeline({
                            title: state.allSchoolInfo.name + '-' + 'quickAsk', // 
                            //link: state.currentHref, // 
                            link: (window.location.href.split('#')[0]+'&redirectUrl='+encodeURIComponent(urlAddToken(window.location.href))).replace('/build/index.html','/weixin/index'),
                            imgUrl: state.imgHost + state.allSchoolInfo.schoolIcon, // 
                            success: function () {
                                // 
                            },
                            cancel: function () {
                                // 
                            }
                        });

                        wx.onMenuShareAppMessage({
                            title: state.allSchoolInfo.name + '-' + 'quickAsk', // 
                            desc: 'quickAsk', // 
//                    desc:urlAddToken(window.location.href),
//                                link: state.currentHref, // 
                            imgUrl: state.imgHost + state.allSchoolInfo.schoolIcon, // 
                            // type: 'musicvideolinklink', // ,musicvideolinklink
                            dataUrl: '', // typemusicvideo
                            success: function () {
                                // 
                            },
                            cancel: function () {
                                // 
                            }
                        });
                    })
                    wx.error(function (err) {
                        // for (var i of Object.keys(err)) {
                        //     alert(i + ' : ' + err[i])
                        // }
                    })
                }, err => {
                    console.log(err)
                })
        }
    }
</script>



