<template>
    <div class="container update-pwd">
        <!--  -->
        <div class="path">
            <i class="comicon-17 icon1-return" :style="{color:$store.state.themeColor}" @click="$router.go(-1)"></i>
            <a href="javascript:void(0);" @click="$router.push('/')" :style="{color:$store.state.themeColor}"></a>
            <i class="comicon-07 icon1-link"></i>
            <span></span>
        </div>
        <div class="text"><img :src=textImgsrc /></div>
        <!--  -->
        <div class="updatepwd-container">
            <div class="login-box">
                <div>
                    <input v-model="mobile" type="number" class="ipt" placeholder="" />
                </div>
                <div class="val-ipt-wp mt30">
                    <input v-model="mobileValidCode" type="number" class="ipt" placeholder="" />
                    <validBtn ref="timerbtn" class="btn" :class="{disabled: validDisable}" :disabled="disabled" v-on:run="getValiCode" :style="{backgroundColor:$store.state.themeColor}" @on-state-change="onStateChange"></validBtn>
                </div>
                <div class="mt30">
                    <input v-model="password" type="password" class="ipt" placeholder="" />
                </div>
                <div class="mt30">
                    <input v-model="repassword" type="password" class="ipt" placeholder="" />
                </div>
                <div class="mt45">
                    <button class="btn" v-bind:class="{disabled: submitDisable}" @click="update" :style="{backgroundColor:$store.state.themeColor}"></button>
                </div>
            </div>
        </div>
        <div class="ajaxLoading">
            <div class="loadInner">
                <v-loading :endFlag="endFlag"></v-loading>
            </div>
        </div>
        <v-tip :options=tip.options :show=tip.show @on-result-change="onResultChange"></v-tip>
    </div>
</template>
<script type="text/javascript">
import "../css/updatePwd.scss"

import urlAddToken from '../utils/urlAddToken.js'

export default {
    data() {
            return {
                disabled: false,
                mobile: '',
                mobileValidCode: '',
                password: '',
                repassword: '',
                textImgsrc: require('../img/login_text_' + this.$store.state.themeColor + '.png'),
                endFlag: true,
                tip: {
                    options: {
                        content: '',
                        autoClose: true
                    },
                    show: false
                }
            }
        },
        components: {
            'v-loading': require('../component/Loading.vue'),
            'v-tip': require('../component/tip.vue'),
            'validBtn': require('../component/validCodeBtn.vue')
        },
        computed: {
            validDisable: function() {
                return !this.mobile || this.disabled
            },
            submitDisable: function() {
                return !this.mobile || !this.mobileValidCode || !this.password || !this.repassword
            }
        },
        methods: {
            getValiCode: function() {
                if (this.validDisable) {
                    return;
                }
                if (!/^13[0-9]{9}$|14[0-9]{9}|15[0-9]{9}$|18[0-9]{9}$|17[0-9]{9}$/.test(this.mobile)) {
                    this.tip.options.content = ''
                    this.tip.show = true
                    return;
                }
                //this.$refs.timerbtn.setDisabled(true); //
                this.disabled = true
                var state = this.$store.state
                var data = {
                    mobile: this.mobile,
                    codeType: 101
                }
                var url = state.host + state.baseUrl + '/common/sendMobileValidCode'
                setTimeout(() => {
                    this.$http.post(url, data, {
                            emulateJSON: true
                        })
                        .then(res => {
                            var data = res.data
                            if (data.code == '000000') {
                                this.$refs.timerbtn.start(); //
                                this.tip.options.content = ''
                                this.tip.show = true
                            } else if(data.code=='020001'){
                                this.$refs.timerbtn.stop(); //
                                this.tip.options.content = data.message
                                this.tip.show = true
                            }
                        }, err => {
                            this.disabled = false
                            this.$refs.timerbtn.stop(); //
//                            this.tip.options.content = err
                            this.tip.options.content = ''
                            this.tip.show = true
                        })
                }, state.timeout)
            },
            update: function() {
                if (this.submitable) {
                    return;
                }
                if (this.password != this.repassword) {
                    this.tip.options.content = ''
                    this.tip.show = true
                    return;
                }
                this.endFlag = false
                var state = this.$store.state
                var data = localStorage.getItem('user')?{
                    id: JSON.parse(localStorage.getItem('user')).id,
                    mobile: this.mobile,
                    mobileValidCode: this.mobileValidCode,
                    password: this.password
                }:{
                    mobile: this.mobile,
                    mobileValidCode: this.mobileValidCode,
                    password: this.password
                }
                var url = state.host + state.baseUrl + '/user/updatePassword'
                var _this=this;
//                setTimeout(() => {
                    this.$http.post(url, data, {
                            emulateJSON: true
                        })
                        .then(res => {
                            this.endFlag = true
                            var data = res.data
//                            this.tip.options.content = data.message
                            this.tip.options.content=''
                            this.tip.show = true
                            setTimeout(function(){
                                _this.$router.push({'path':'/login'})
                            },2000)
                        }, err => {
                            this.endFlag = true
                            this.tip.options.content = err
                            this.tip.show = true
                        })
//                }, state.timeout)
            },
            onResultChange: function() {
                this.tip.show = false
            },
            onStateChange: function(val) {
                this.disabled = val
            }
        },
        created() {
            var state = this.$store.state
            document.title = state.allSchoolInfo.name + '-'


        },
        mounted(){





            var state=this.$store.state;
            wx.ready(function() {
                wx.onMenuShareTimeline({
                    title: state.allSchoolInfo.name+'-'+'', // 
                    //link: state.currentHref, // 
                    imgUrl: state.imgHost + state.allSchoolInfo.schoolIcon, // 
                    success: function() {
                        // 
                    },
                    cancel: function() {
                        // 
                    }
                });

                wx.onMenuShareAppMessage({
                    title: state.allSchoolInfo.name+'-'+'', // 
                    desc: '', // 
//                    desc:urlAddToken(window.location.href),
//                                link: state.currentHref, // 
                    link:(window.location.href.split('#')[0]+'&redirectUrl='+encodeURIComponent(urlAddToken(window.location.href))).replace('/build/index.html','/weixin/index'),
                    imgUrl: state.imgHost + state.allSchoolInfo.schoolIcon, // 
                    // type: 'musicvideolinklink', // ,musicvideolinklink
                    dataUrl: '', // typemusicvideo
                    success: function() {
                        // 
                    },
                    cancel: function() {
                        // 
                    }
                });
            })
        }
}
</script>



